.sidebar {
    background-color: var(--background_color);
    float: right;
    color: white;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2;
    width: 45vw;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    transform: translateX(-100vw);
}
.sidebar__link {
    list-style: none;
    font-size: 14px;
    text-transform: uppercase;
    padding: 20px;
    cursor: pointer;
    transition: 0.3s;
    color: var(--text-color)
}
.sidebar__link:hover {
    color: var(--hover_color);
    transform: scale(1.15);
}
.sidebar__links a {
    color: var(--text-color);
    text-decoration: none;
}
.sidebar-icon {
    padding-top: 8px;
    padding-right: 5px;
    font-size: 18px;
}
.sidebar__cancel {
    padding-top: 15px;
    padding-right: 10px;
    font-size: 25px;
    cursor: pointer;
    color: var(--text-color)
}
.sidebar__cancel:hover {
    transform: scale(1.009);
}
#show-sidebar {
    transform: translateX(0);
    transition: .5s;
}
#hide-sidebar {
    transform: translateX(-100vw);
    transition: .5s;
}
@media screen and (min-width: 768px) {
    .sidebar {
        display: none;
    }
    .sidebar-icon {
        padding-top: 8px;
        padding-right: 5px;
        font-size: 16px;
    }
}
@media screen and (max-width: 375px) {
    .sidebar {
        width: 55vw;
    }
}
