.tech-stack-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px 0px 30px 0px;
}

.tech-stack-title-container h2 {
    font-size: 30px;
    color: var(--dark-text-color);
    padding: 0px 5px 3px 0px;
}

.tech-stack-title-container hr {
    margin-left: 80px;
    width: 130px;
    height: 2px;
    background-color: var(--background_color);
    border-width: 0px;
}

.skills-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.skills-line {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.skill-icon-container {
    margin: 20px;
    text-align: center;
    transition: transform 0.4s ease-in;
}

.skill-icon-container:hover {
    transform: scale(1.2);
}

.skill-icon {
    font-size: 2.8rem;
}

.skill-icon-text {
    transition: color 0.2s ease-in-out;
}

.skill-icon-container:hover .skill-icon-text {
    color: var(--background_color);
}


@media screen and (max-width: 910px) {
    .tech-stack-title-container h2 {
        font-size: 25px;
    }

    .skill-icon {
        font-size: 2.5rem;
    }

    .skill-icon-container {
        margin: 15px;
    }

    .tech-stack-title-container hr {
        width: 100px;
    }
    .skill-icon-text {
        font-size: 12px;
    }
}

@media screen and (max-width: 768px) {

    .skill-icon {
        font-size: 2.3rem;
    }

    .skill-icon-container {
        margin: 10px;
    }
    .skill-icon-container:hover {
        transform: scale(1.05);
    }
    .skill-icon-text {
        font-size: 10px;
    }

}