.navbar {
    background-color: var(--background_color);
}
.navbar-wrapper {
    color: var(--text-color);
    display: flex;
    justify-content: right;
    align-items: center;
    max-width: 1000px;
    margin: 0 auto;
    padding: 15px 20px ;
}
.navbar-logo {
    flex: .6;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.navbar-links {
    padding-top: 15px;
    flex: .4;
    display: flex;
    justify-content: space-evenly;
}
.navbar-links a {
    color: var(--text-color);
    text-decoration: none;
}
.navbar-link {
    list-style: none;
    cursor: pointer;
    font-size: 20px;
    transition: .2s;
}
.navbar-link:hover {
    color: var(--hover_color);
    transform: scale(1.15);
}
.navbar-burgerIcon {
    font-size: 28px;
    cursor: pointer;
    display: none;
}
/*.navbar-link {*/
/*    font-size: 20px;*/
/*}*/
.navbar-icon {
    padding-top: 8px;
    padding-right: 5px;
    font-size: 24px;
}
.navbar-curved-shape {
    margin-top: -25px
}

@media(max-width: 1110px) {
    .navbar-icon {
        font-size: 22px;
    }
    .navbar-link {
        font-size: 18px;
    }
}
@media(max-width: 810px) {
    .navbar-icon {
        font-size: 20px;
    }
    .navbar-link {
        font-size: 16px;
    }
    .navbar-curved-shape {
        margin-top: -10px
    }
}

@media(max-width: 768px) {
    .navbar-links {
        display: none;
    }
    .navbar-burgerIcon {
        display: block;
    }
}
