.travel-pops-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 180px;
    width: 180px;
    margin: 15px;
    cursor: pointer;
}
.travel-pops-container:hover {
    transform: scale(1.10);
    transition: .5s ease-out;
}
.travel-pops-image {
    display: flex;
    width: 100%;
    height: 100%;
}
.travel-pops-image img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border-width: 2px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.travel-pops-text {
    padding-right: 25px;
    padding-top: 10px;
}

@media screen and (max-width: 1110px) {
    .travel-pops-container {
        height: 160px;
        width: 160px;
        margin: 20px;
    }
    .travel-pops-image img {
        width: 150px;
        height: 150px;
    }

    .travel-pops-text {
        font-size: 14px;
        padding-right: 8px;
    }
}

@media screen and (max-width: 910px) {
    .travel-pops-container {
        height: 150px;
        width: 150px;
    }
    .travel-pops-image img {
        width: 140px;
        height: 140px;
    }
    .travel-pops-text {
        font-size: 12px;
    }
}

@media screen and (max-width: 810px) {
    .travel-pops-container {
        height: 120px;
        width: 120px;
        margin: 10px;
    }
    .travel-pops-image img {
        width: 90px;
        height: 90px;
    }
    .travel-pops-text {
        font-size: 11px;
        padding-right: 20px;
    }
}
@media screen and (max-width: 375px) {
    .travel-pops-container {
        margin: 5px;
    }
    .travel-pops-image img {
        width: 90px;
        height: 90px;
    }
    .travel-pops-text {
        font-size: 11px;
        padding-right: 20px;
    }
}
