:root {
    --background_color: #9CAF88; /* sage green */
    --hover_color: white;
    --text-color: #FFF9C2;
    --highlighted-text-color: #9CAF88;
    --default-font: "Poppins Semi Bold";
    --dark-text-color: #506369;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: var(--default-font);
    outline: none;
}

body {
    margin: 0;
    overflow-y: auto;
    font-family: "Poppins Regular";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--dark-text-color)
}

@font-face {
    font-family: "Poppins Regular";
    font-style: normal;
    font-weight: normal;
    src: url("./assets/fonts/Poppins-Regular.ttf");
}

@font-face {
    font-family: "Poppins Light Italic";
    font-style: normal;
    font-weight: normal;
    src: url("./assets/fonts/Poppins-ExtraLightItalic.ttf");
}
@font-face {
    font-family: "Poppins Semi Bold";
    font-style: normal;
    font-weight: normal;
    src: url("./assets/fonts/Poppins-SemiBold.ttf");
}


.btn {
    border-radius: 40px;
    padding: 14px 0;
    width: 120px;
    cursor: pointer;
    transition: 0.2s;
    font-family: "Poppins Semi Bold";
}

.btn:active {
    transform: translateY(2px);
}

.primary-button {
    border: 2px solid linen;
    font-size: 12px;
    background-color: var(--background_color);
    color: var(--text-color);
}

.primary-button:hover {
    border: 2px solid var(--background_color);
    transform: scale(1.10);
    background-color: var(--text-color);
    color: var(--background_color);
}
